<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;  
             &nbsp; .    &nbsp;
            &nbsp;     .
          </p>

          <ul class="dictionary">
            <OrderInfoItem title="" :value="orderInfo.name" />
            <OrderInfoItem title=" " :value="orderInfo.address" />
            <OrderInfoItem title="" :value="orderInfo.phone" />
            <OrderInfoItem title="Email" :value="orderInfo.email" />
            <OrderInfoItem
              title=" "
              :value="orderInfo.paymentType"
            />
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="item in orderInfo.basket.items"
              :key="item.id"
            >
              <h3>
                {{ item.productOffer.title }}
                <b class="product-amount" v-show="item.quantity > 1"
                  >x{{ item.quantity }}</b
                >
              </h3>
              <b> {{ item.price | numberFormat }} </b>
              <span>: {{ item.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>
              {{ orderInfo.deliveryType.title }}:
              <b
                >{{
                  orderInfo.deliveryType.price > 0
                    ? `${orderInfo.deliveryType.price} `
                    : ""
                }}
              </b>
            </p>
            <p>
              : {{ getRightWord(orderInfo.basket.items.length) }}  
              <b>{{ orderInfo.totalPrice | numberFormat }} </b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
/* eslint-disable quotes */
import numberFormat from "@/helpers/numberFormat";
import getRightWord from "@/helpers/getRightWord";
import OrderInfoItem from "@/components/OrderInfoItem.vue";

export default {
  components: { OrderInfoItem },
  created() {
    if (
      // eslint-disable-next-line operator-linebreak
      !this.$store.state.orderInfo &&
      this.$store.state.orderInfo !== this.$route.params.id
    ) {
      this.$store.dispatch("loadOrderInfo", this.$route.params.id);
    }
  },
  methods: {
    getRightWord,
  },
  computed: {
    orderInfo() {
      return this.$store.state.orderInfo;
    },
  },
  filters: {
    numberFormat,
  },
};
</script>
